import React from "react";
import "./AppTodoItem.css";

const AppTodoItem = (props) => {
  return (
    <div className={props.AppTodoItemClasses} id={props.indexOfItem}>
      <div>{props.name}</div>
      <button className={"ItemDelete"} onClick={props.deleteItem}>
        &#9746;
      </button>
    </div>
  );
};

export default AppTodoItem;
