import React from "react";
import "./AppTodoList.css";
import AppTodoItem from "../AppTodoItem/AppTodoItem";

const AppTodoList = (props) => {
  let list = props.todoList.map((item, index) => {
    return (
      <AppTodoItem
        indexOfItem={index}
        key={index}
        name={item.name}
        AppTodoItemClasses={props.AppTodoItemClasses}
        deleteItem={props.deleteItem.bind(this)}
      />
    );
  });

  return <div className={"AppTodoList"}>{list}</div>;
};

export default AppTodoList;
