import React from "react";
import "./AppAddItem.css";

const AppAddItem = (props) => {
  return (
    <div className={"AppAddItem"}>
      <p>Добавить: </p>
      <input />
      <button className={"AddItemButton"} onClick={props.addItem}>
        &#9745;
      </button>
    </div>
  );
};

export default AppAddItem;
