import React, { Component } from "react";
import "./App.css";
import AppHeader from "./AppHeader/AppHeader";
import AppBody from "./AppBody/AppBody";
import AppAddItem from "./AppAddItem/AppAddItem";
import AppFooter from "./AppFooter/AppFooter";

export default class App extends Component {
  state = {
    todoList: [],
    AppTodoItemClasses: ["AppTodoItem"],
  };

  addItem = () => {
    if (document.querySelector("input").value === "") return;

    let newTodoList = this.state.todoList;
    newTodoList.push({
      name: document.querySelector("input").value,
    });

    this.setState({
      todoList: newTodoList,
    });

    document.querySelector("input").value = "";
  };

  deleteItem = (event) => {
    let newTodoList = this.state.todoList;
    newTodoList.splice(event.target.parentElement.id, 1);

    this.setState({
      todoList: newTodoList,
    });
  };

  clearAll = () => {
    let newTodoList = [];

    this.setState({
      todoList: newTodoList,
    });
  };

  render() {
    return (
      <div className="App">
        <AppHeader />
        <AppBody
          todoList={this.state.todoList}
          AppTodoItemClasses={this.state.AppTodoItemClasses}
          deleteItem={this.deleteItem}
        />
        <AppAddItem addItem={this.addItem} />
        <AppFooter clearAll={this.clearAll} />
      </div>
    );
  }
}
