import React from "react";
import "./AppBody.css";
import AppTodoList from "../AppTodoList/AppTodoList";

const AppBody = (props) => {
  return (
    <div className={"AppBody"}>
      <AppTodoList
        todoList={props.todoList}
        AppTodoItemClasses={props.AppTodoItemClasses}
        deleteItem={props.deleteItem}
      />
    </div>
  );
};

export default AppBody;
